@import "../../../style/variables.scss";

.subs_table {
    table {

        width: 100%;
        padding: 0;
        table-layout: fixed;
        border-collapse: collapse;
        text-align: left;

        th,
        td {
            border: 1px solid black;
            background-color: rgb(255, 255, 255);
            padding: 0;
            margin: 0;
            padding-left: 10px;
            overflow-x: auto;


        }

        td {
            font-weight: lighter;
            height: 25px;

        }

        input {
            width: 95%;
            overflow-x: auto;
            border: none;
        }

        span {

            display: flex;
            justify-content: center;
            align-items: center;

            button{
                background-color:$bg-top-menu-btn-default;
                color: white;
                border: 0;
                border-radius: 3px;
            }

        }

        



    }




}